import Vue from 'vue'
import App from './App.vue'
import Vuelidate from 'vuelidate'
import store from './store'
import './quasar'

import router from '@/router'

import Quasar from 'quasar/src/vue-plugin.js';import QImg from 'quasar/src/components/img/QImg.js';import QToolbar from 'quasar/src/components/toolbar/QToolbar.js';import QToolbarTitle from 'quasar/src/components/toolbar/QToolbarTitle.js';import QInput from 'quasar/src/components/input/QInput.js';import QForm from 'quasar/src/components/form/QForm.js';import QBtnDropdown from 'quasar/src/components/btn-dropdown/QBtnDropdown.js';import QBtn from 'quasar/src/components/btn/QBtn.js';

var VueScrollTo = require('vue-scrollto');

Vue.use(VueScrollTo);

Vue.use(Quasar, {
  components: {
    QImg,
    QToolbar,
    QToolbarTitle,
    QInput,
    QForm,
    QBtnDropdown,
    QBtn
  }
})

Vue.use(Vuelidate)

Vue.config.productionTip = false

new Vue({
  store,
  render: h => h(App),
  router
}).$mount('#app')

Vue.prototype.$eventBus = new Vue()